/** @format */

import React from "react";
import classes from "./NoLinkedIn.module.css";
import sadIcon from "../../../../assets/icons/sadicon.svg";
import { CustomButton } from "../../../exports";

const NoLinkedIn = ({ setReload = (f) => f }) => {
  return (
    <div className={classes.Type0_Account}>
      <div className={classes.Type0_Note}>
        <img src={sadIcon} alt="sad" />
        <p>We could not find any LinkedIn business account</p>
        <main className={classes.Type0_Link}>
          <CustomButton
            className={classes.Type0_Button}
            onClick={() =>
              window.open(
                `https://www.linkedin.com/company/setup/new`,
                "_blank"
              )
            }
          >
            Create business account
          </CustomButton>
        </main>
      </div>
      <p className={classes.Done_Linking}>
        Done linking? <span onClick={() => setReload()}>Resume Setup!</span>
      </p>
    </div>
  );
};

export default NoLinkedIn;
